import React from 'react'
import Chart from "react-google-charts";
import Select from "react-select"
import { Slider } from "@mui/material";
import { useEffect, useState } from "react";

const formatter = new Intl.NumberFormat('uk-UA', {
    style: 'currency',
    currency: 'UAH'
});
function createCustomHTMLContent(info) {
    return '<div class="m-2 info">'
     + '<small>' + info.reg_date + '</small>'
     + '<h6 class="mb-1">' + formatter.format(info.amount) + '</h6> '
    //+ info.description
     + info.custom_comment
     + '<hr class="m-1"/>'
     + '<i>' + info.tags.split(',').map(t => '#' + t).join(' ') + '</i>'
     + '</div>';
}
export default function Statistics() {
    const [rawData, setRawData] = useState([]);
    const [range, setRange] = useState([0, 0]);
    const [filters, setFilters] = useState([]);
    const [onlyFit, setOnlyFit] = useState(true);
    const [data, setData] = useState([]);

    useEffect(() => {
      fetch(window.vars.host + '/api.php?init_data=' + encodeURIComponent(window.Telegram.WebApp.initData) + '&method=getPayments')
        .then((response) => {
          if (!response.ok) {
            window.Telegram.WebApp.showAlert(' ');
            throw Error(response.status);
          }
            return response;
        })
        .then((response) => response.json())
        .then((raw_data) => setRawData(raw_data.map((pm, i) => {return {...pm, i: i}})));
    }, []);
    useEffect(() => {
      if(rawData.length === 0) return;
      setRange([rawData.length > 10 ? rawData.length - 10 : 1, rawData.length]);
    }, [rawData]);
    useEffect(() => {
      if(rawData.length === 0) return;
      setData(rawData.filter((payment) => onlyFit ? does_range_fit(payment) : does_payment_fit(payment)));
      document.querySelector('.select-off input').disabled = true
    }, [range, filters, onlyFit, rawData]);

    const does_tag_fit = (tag, payment) => {
        if(tag === 'grow') return payment.amount > 0;
        if(tag === 'fall') return payment.amount < 0;
        if(payment.tags.split(',').indexOf(tag.substring(1)) === -1) return false;
        return true;
    }
    const does_range_fit = (payment) => {
      return payment.i + 1 >= range[0] && payment.i < range[1];
    }
    const does_payment_fit = (payment) => {
      if(does_range_fit(payment) === false) return false;
      if(filters.length === 0) return true;
      for(let i = 0; i < filters.length; i++){
        if(does_tag_fit(filters[i], payment) === true) return true;
      }
      return false;
    }
  
    const prepareData = (raw_data) => {
      let rows = [];
      let sum = 0;
      raw_data.forEach((payment, i) => {
        let open = sum;
        let close = sum + payment.amount;
        rows.push([
          /*new Date(payment.reg_date)*/'n',
          Math.min(open, close),
          open,
          close,
          Math.max(open, close),
          createCustomHTMLContent(payment),
          'color:' + (does_payment_fit(payment) ? (payment.amount > 0 ? '#0f9d58' : '#a52714') : '#d3d3d3')
        ]);
        sum += payment.amount;
      });
      return rows;
    }
    function getTagsOption(work_data, count = false){
      let data =  [
        {value: 'grow', label: ''},
        {value: 'fall', label: ''},
        ...[...new Set(work_data.map(p => p.tags.split(',')).reduce((p, n) => p.concat(n), []))].sort().map(t => {return {value: '#' + t, label: '#' + t}})
      ].map(opt => {return {
        value: opt.value,
        label: opt.label 
          + ' (' + rawData.filter(payment => does_tag_fit(opt.value, payment) && does_range_fit(payment)).length + ')'
      }});
      if(count){
        data.forEach(opt => {
          let sum = rawData.filter(payment => does_tag_fit(opt.value, payment) && does_range_fit(payment)).reduce((pr, curr) => pr + curr.amount, 0);
          opt.count = sum;
          opt.label += ', <span class="' + (sum > 0 ? 'grow' : 'fall') + '">' + formatter.format(sum) + (sum > 0 ? ' &#x2197;' : ' &#x2198;') + '</span>';
        });
      }
      return data;
    }
    function setFilterTag(data){
      let new_filters = [];
      for(let i = 0; i < data.length; i++){
        if(data[i].value === 'grow' && new_filters.indexOf('fall') !== -1)
          return setFilters(['grow']);
        if(data[i].value === 'fall' && new_filters.indexOf('grow') !== -1)
          return setFilters(['fall']);
        new_filters.push(data[i].value);
      }
      setFilters(new_filters);
    }
    return rawData.length === 0 ? <h3>   )</h3> : (<div className="">
      <h3> </h3>
      <hr/>
      <div className="chart" id="main_chart" /*onClick={handleClick} onTouchMove={hangleTouchMove} onTouchStart={hangleTouchStart} onTouchEnd={handleTouchEnd}*/>
        {
          filters.length > 0
          ? <span className="floatingTrigger" onClick={() => setOnlyFit(!onlyFit)}>
            {
              onlyFit
              ? <span key={'on'}><i className="fas fa-eye"></i></span>
              : <span key={'off'}><i className="fas fa-eye-slash"></i></span>
            }
          </span>
          : null
        }
        
        {
          data.length === 0
          ? <p className="m-0">    <i>{filters.filter((tag) => rawData.filter(payment => does_tag_fit(tag, payment) && does_range_fit(payment)).length === 0).join(', ')}</i>   :/</p> 
          : <Chart
              //style={{ pointerEvents: 'none' }}
              width={"100%"}
              chartType="CandlestickChart"
              loader={<div>Loading Chart</div>}
              data={[
                ["t", "a", "b", "c", "d",
                  {role: "tooltip", type: "string", p: {html: true}}, 
                  {role: 'style'}
                ],
                ...prepareData(data)
              ]}
              options={{
                legend: {position: 'none'},
                chartArea: {'width': '75%', 'height': '90%'},
                tooltip: {isHtml: true},
                //bar: { groupWidth: '100%' },
                hAxis: {
                  textPosition: 'none',
                  color: '#333',
                },
                candlestick: {
                  // fallingColor: { strokeWidth: 0, fill: '#a52714'}, // red
                  // risingColor: { strokeWidth: 0, fill: '#0f9d58'}   // green
                }
              }}
            />
          }
      </div>
      <Slider 
        value={range}
        marks={true}
        valueLabelDisplay="auto"
        onChange={(e, new_val) => setRange(new_val)}
        valueLabelFormat={(val) => /*-(rawData.length - val + 1)*/rawData[Math.round(val) - 1].reg_date}
        min={1}
        max={rawData.length}
      />
      <div className="select-off">
        <Select 
          isMulti
          value={getTagsOption(rawData).filter(opt => filters.indexOf(opt.value) !== -1)}
          options={getTagsOption(/*data*/rawData, true)}
          onChange={setFilterTag}
          autoFocus={false}
          // blurInputOnSelect={true}
          // isSearchable={false}
          menuPosition="fixed"
          styles={{
            option: (styles, tag) => {
              let styledata = {...styles};
              if(tag.data.value === 'grow'){
                styledata.color = 'darkgreen';
                styledata.backgroundColor = '#e5f9e5';
              }
              else if(tag.data.value === 'fall'){
                styledata.color = 'darkred';
                styledata.backgroundColor = '#fde3e3';
              }
              // else{
              //   styledata.color = tag.count > 0 ? 'darkgreen' : 'darkred';
              // }
              return styledata
            },
            multiValue: (styles, color) => {
              let styledata = {...styles};
              if(color.data.value === 'grow'){
                styledata.color = 'darkgreen';
                styledata.backgroundColor = '#e5f9e5';
              }
              else if(color.data.value === 'fall'){
                styledata.color = 'darkred';
                styledata.backgroundColor = '#fde3e3';
              }
              return styledata
            },
            multiValueLabel: (styles, color) => {
              let styledata = {...styles};
              if(color.data.value === 'grow'){
                styledata.color = 'darkgreen';
              }
              else if(color.data.value === 'fall'){
                styledata.color = 'darkred';
              }
              return styledata
            },
            multiValueRemove: (styles, color) => {
              let styledata = {...styles};
              if(color.data.value === 'grow'){
                styledata[':hover'] = {
                  backgroundColor: 'green',
                  color: '#e5f9e5'
                }
              }
              else if(color.data.value === 'fall'){
                styledata[':hover'] = {
                  backgroundColor: '#a52714',
                  color: '#fde3e3'
                }
              }
              return styledata
            },
          }}
          formatOptionLabel={function(data) {
            return (
              <span dangerouslySetInnerHTML={{ __html: data.label }} />
            );
          }}
        />
      </div>
    </div>)
}






