import React, { Fragment, useEffect, useState } from 'react'
import Switch from '@mui/material/Switch';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormHelperText from '@mui/material/FormHelperText';

const formatter = new Intl.NumberFormat('uk-UA');
const codes = {
  '980': {
    code: 'UAH',
    symbol: ''
  },
  '840': {
    code: 'USD',
    symbol: '$'
  },
  '978': {
    code: 'EUR',
    symbol: ''
  }
}
export default function Accounts() {
  const [clientInfo, setClientInfo] = useState(null);
  useEffect(() => {
    fetch(window.vars.host + '/api.php?init_data=' + encodeURIComponent(window.Telegram.WebApp.initData) + '&method=info')
      .then((response) => {
        if (!response.ok) {
          window.Telegram.WebApp.showAlert(' ');
          throw Error(response.status);
        }
          return response;
      })
      .then((response) => response.json())
      .then((data) => setClientInfo(data));
  }, []);

  const accountChange = (data, account) => {
    fetch(window.vars.host + '/api.php?init_data='
       + encodeURIComponent(window.Telegram.WebApp.initData)
       + '&method=accountManage' 
       + '&action=' + (data ? 'add' : 'remove')
       + '&account=' + account)
      .then((response) => {
        if (!response.ok) {
          window.Telegram.WebApp.showAlert('    (');
          //window.location.reload();
          throw Error(response.status);
        }
      });
  }

  return clientInfo === null ? <h3> ...</h3> : (
    <Fragment>
      <h3 className=''><i className="fas fa-wallet"></i> {clientInfo.name}</h3>
      {
        clientInfo.accounts.sort((a, b) => a.maskedPan[0] > b.maskedPan[0] ? 1 : -1).map(account => {
          return (
            <div className='flex-table' key={account.id}>
              <p className='m-0 mt-2'>
                {account.maskedPan[0]} <i className='other nowrap'><sub>({account.type}, {formatter.format(account.balance/100)} {codes[account.currencyCode].symbol})</sub></i>
              </p>
              <Switch 
                size=''
                defaultChecked={window.data.accounts.map(a => a.id).indexOf(account.id) === -1 ? false : true}
                onChange={(data) => accountChange(data.target.checked, account.id)}
              />
            </div>
          )
        })
      }
    </Fragment>
  );
}
