import { useState } from "react";
import Accounts from "./Accounts";
import Statistics from "./Statistics";

function App() {
  const [tab, setTab] = useState(window.data.accounts.length === 0 ? 'accounts' : 'statistics');
  return <div className="m-3 row">
    <div className="btn-group">
      <button type="button" className={"btn " + (tab === 'accounts' ? 'unselected' : '')} onClick={() => setTab('statistics')}></button>
      <button type="button" className={"btn " + (tab === 'statistics' ? 'unselected' : '')} onClick={() => setTab('accounts')}></button>
    </div>
    <hr className="mt-3"/>
    {
      tab === 'accounts'
      ? <Accounts />
      : <Statistics />
    }

  </div>
}

export default App;
