import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';

window.vars = {
  host: document.location.origin
}
if(document.location.origin.search('localhost') !== -1)
  window.vars.host = 'https://monochart.solarisedigle.pp.ua';

await fetch(window.vars.host + '/api.php?init_data=' + encodeURIComponent(window.Telegram.WebApp.initData) + '&method=-')
  .then((response) => {
    if (!response.ok) {
      window.Telegram.WebApp.showAlert(' ');
      throw Error(response.status);
    }
      return response;
  })
  .then((response) => response.json())
  .then((data) => {
    window.data = data/*.map((data, i) => {return {...data, i: i}})*/;
});
const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);

